// Colors definitions
$black_font: #231f20;

$grey_dark: #575e62;
$grey_light: #d1dce3;

$blue_dark: #00649d;
$blue_light: #148bcb;
$blue_brand: #0280c3;

$red_dark: #ff6347;

$white: #fff;

$green-jewel-4: #52a06b;

$yellow-butter-5 : #fecb00;

// Settings
$brand_color: $blue_brand;
$background_color: $white;
$primary_color: $grey_dark;
$font_color: $black_font;
$link_color: $blue_light;

$border_color: $grey_light;

$danger_color: $red_dark;
$success_color: $green-jewel-4;
$error_color: $red_dark;
$processing_color: $yellow-butter-5;

$processing_gradient: 0, $processing_color, $processing_color;
$success_gradient: 0, $success_color, $success_color;
$error_gradient: 0, $danger_color, $danger_color;

// Modifiers
$hover_lighten: 10;
