






















































































































@import "@/assets/scss/variables.scss";

#sidebar {
  width: $sidebar-size;
  z-index: 5;
  @include breakpoint(mobile) {
    display: flex;
    flex-direction: row-reverse;
    width: auto;
    height: $sidebar-size;
  }
  background-color: $primary-color;

  button {
    display: block;
    width: calc(100% - 10px);
    color: $secondary-color;
    border: 1px solid $secondary-color;
    background-color: transparent;
    padding: 10px 12px;
    margin: 5px;
  }
}
