
































@import "@/assets/scss/variables.scss";

main {
  flex: 1;
  background-color: $background-color;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  touch-action: auto;
  #columns {
    height: 100%;
    display: flex;
  }
}

.list-leave-to {
  transform: translate(0% ,-100%);
}
.list-leave-active, .list-move {
  transition: transform 0.5s;
}
